import {HttpClient, HttpHeaders} from '@angular/common/http';
import { Injectable } from '@angular/core';
import 'rxjs/add/operator/map';


@Injectable()
export class Post{
    url_site: string = 'http://localhost/delivery/';
    server: string = this.url_site + 'api/';
    
    url_site_img_produtos: string = this.url_site + 'images/produtos/';
    url_site_img_cat: string = this.url_site + 'images/categorias/';
    url_site_img: string = this.url_site + 'images/';

    constructor(private http : HttpClient){
       
    }

  
     dadosApi(dados: any, api: string){
            const httpOptions = {
                headers: new HttpHeaders({'Content-Type' : 'application/json'})
                }
                console.log("httpOptions");
                console.log(httpOptions);
                console.log("Dados");
                console.log(dados);
            let url = this.server + api;
            let jsonTotal = this.http.post(url, JSON.stringify(dados), httpOptions).map(res => res);
            console.log("API TESTE");
            console.log(jsonTotal);
            return jsonTotal;
        }
}
